 <template>
  <v-main>
    <NavAndFooter />
    <v-card class="mx-auto" max-width="100%" tile>
      <v-col>
        <p style="text-align: center; margin: 0;">
          <v-avatar size="100">
            <v-img :src="`${user.photo}`"></v-img>
          </v-avatar>
        </p>
      </v-col>
      <v-list-item color="rgba(0, 0, 0, .4)">
        <v-list-item-content justify="center">
          <v-list-item-title class="title" style="text-align: center;">Логин: {{username}}</v-list-item-title>
          <v-list-item-title class="title" style="text-align: center;">Имя: {{user.first_name}}</v-list-item-title>
          <v-list-item-title class="title" style="text-align: center;">Фамилия: {{user.last_name}}</v-list-item-title>
          <v-list-item-title class="title" style="text-align: center;">Почта: {{user.email}}</v-list-item-title>
          <div style="margin-top: 15px; margin-bottom: 10px;">
            <v-row justify="center">
              <v-dialog v-model="dialog" persistent max-width="600px">
                <template v-slot:activator="{ on, attrs }">
                  <v-btn color="primary" dark v-bind="attrs" v-on="on">Pедактировать</v-btn>
                </template>
                <v-card>
                  <v-card-title>
                    <span class="text-h5">Редактировать профиль</span>
                  </v-card-title>
                  <v-card-text>
                    <v-container>
                      <v-row>
                        <v-col cols="12">
                          <v-file-input
                            accept="image/*"
                            label="Фото"
                            prepend-icon="mdi-camera"
                            @change="onFileChange"
                          ></v-file-input>
                        </v-col>
                        <v-col cols="12" sm="6" md="6">
                          <v-text-field
                            label="Логин*"
                            v-model="login"
                            required
                            clearable
                            :value="username"
                            @change="username = $event"
                          ></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="6">
                          <v-text-field label="E-mail*" v-model="email" required clearable></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="6">
                          <v-text-field label="Имя*" v-model="name" required clearable></v-text-field>
                        </v-col>
                        <v-col cols="12" sm="6" md="6">
                          <v-text-field label="Фамилия*" v-model="family" required clearable></v-text-field>
                        </v-col>
                      </v-row>
                    </v-container>
                    <small>*Обязательные поля</small>
                  </v-card-text>
                  <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="blue darken-1" text @click="dialog = false">Закрыть</v-btn>
                    <v-btn color="blue darken-1" type="submit" text @click="updateAvatar">Photo</v-btn>
                    <v-btn color="blue darken-1" type="submit" text @click="updateUser">Сохранить</v-btn>
                  </v-card-actions>
                </v-card>
              </v-dialog>
            </v-row>
          </div>
        </v-list-item-content>
      </v-list-item>
    </v-card>
  </v-main>
</template>
<script>
import $ from "jquery";
import NavAndFooter from "../components/NavAndFooter";
export default {
  components: { NavAndFooter },
  metaInfo: {
    title: "Профиль - Цитадель"
  },
  data() {
    return {
      user: [],
      dialog: false,
      link: "",
      login: "",
      email: "",
      name: "",
      family: ""
    };
  },
  created() {
    this.username = localStorage.getItem("username");
    this.users();
  },
  methods: {
    onFileChange(file) {
      this.link = file;
    },
    users() {
      $.get("http://localhost:8002/api/user/" + this.username + "/", data => {
        this.user = data;
        (this.name = data.first_name),
          (this.login = this.username),
          (this.family = data.last_name),
          (this.email = data.email);
        //this.link = data.photo;
      });
    },
    updateAvatar() {
      let formData = new FormData();
      console.log(this.link);
      formData.append("photo", this.link);
      formData.append("csrfmiddlewaretoken", window.CSRF_TOKEN);

      $.ajax({
        url: "http://localhost:8002/api/user-avatar/" + this.username + "/",
        data: formData,
        processData: false,  // tell jQuery not to process the data
        contentType: false,
        type: "POST",
        success: function(data) {
          console.log('done')
        },
        error: function(response) {
          console.log(this.data);
          let err = response.responseJSON;
          for (let key in err) {
            alert(key, err[key].toString());
          }
        }
      });
    },
    updateUser() {
      const login = this.login;
      $.ajax({
        url: "http://localhost:8002/api/update_profile/" + this.username + "/",
        data: {
          username: this.login,
          first_name: this.name,
          last_name: this.family,
          email: this.email
        },
        type: "PUT",
        success: function(data) {
          if (localStorage.getItem("username") !== login) {
            localStorage.removeItem("username");
            localStorage.setItem("username", login);
          }
          location.reload();
        },
        error: function(response) {
          console.log(this.data);
          let err = response.responseJSON;
          for (let key in err) {
            alert(key, err[key].toString());
          }
        }
      });
      this.updateAvatar();
    }
  }
};
</script>